<template>
  <div class="m-geo">
    <div class="position">
      <i class="el-icon-location"></i>
      <span>{{$store.state.position.name}}</span>
      <router-link class="changeCity" :to="{name:'changecity'}">切换城市</router-link>
      <span>
        [
        <a href="#" v-for="(item,index) in nearCity" :key="item+'_' +index"> {{item.name}} </a>

        ]
      </span>
    </div>
    <div class="m-user" v-if="!$store.state.userName">
      <router-link class="login" to="/login">立即登录</router-link>
      <router-link class="register" to="/register">注册</router-link>
    </div>
  </div>
</template>
<script>
import api from '@/api/index.js'
export default {
  data () {
    return {
      nearCity: []
    }
  },
  watch: { // 侦听vuex数据中的position变化时，更新附近城市
    '$store.state.position' () {
      this.nearCity = this.$store.state.position.nearCity
    }

  },
  created () {
    api.getPosition()
      .then(res => {
        this.$store.dispatch('setPosition', res.data.data)
        this.nearCity = res.data.data.nearCity
      })
  }
}
</script>
