<template>
  <div class="search-panel">
    <el-row class="m-header-searchbar">
      <el-col :span="3" class="left">
        <router-link to="/index">
          <img src="http://s0.meituan.net/bs/fe-web-meituan/e5eeaef/img/logo.png" alt="美团" />
        </router-link>
      </el-col>
      <el-col :span="15" class="center">
        <div class="wrapper">
          <el-input
            v-model="searchWord"
            placeholder="搜索商家或地点"
            @focus="focusFunc"
            @blur="blurFunc"
            @input="inputFn"
          ></el-input>
          <el-button type="primary" icon="el-icon-search"></el-button>
          <dl class="hotPlace" v-if="hotPlace">
            <dt>热门搜索</dt>
            <dd :key="item+'-'+index" v-for="(item,index) in hotSearchList">
              <router-link :to="{name:'goods',params:{name:item}}">{{item}}</router-link>
            </dd>
          </dl>
          <dl class="searchList" v-if="searchPlace">
            <dd :key="index" v-for="(item,index) in searchList">
              <router-link :to="{name:'goods',params:{name:item}}">{{item}}</router-link>
            </dd>
          </dl>
        </div>
        <p class="suggest">
          <router-link
            :key="item+'~'+index"
            :to="{name:'goods',params:{name:item}}"
            v-for="(item,index) in suggestList"
          >{{item}}</router-link>
        </p>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import api from '@/api/index.js'
export default {
  data () {
    return {
      searchWord: '',
      isFocus: false,
      hotSearchList: [],
      suggestList: [],
      searchList: []
      //   hotPlace: false,
      //   searchList: false,
    }
  },
  methods: {
    focusFunc () {
      this.isFocus = true
    },
    blurFunc () {
      setTimeout(() => {
        this.isFocus = false
      }, 300)
    },
    inputFn () { // 输入时数据请求：输入框搜索关键字列表
      api.search()
        .then(res => {
          const val = this.searchWord
          this.searchList = res.data.data.list.filter((el, index) => {
            return el.includes(val)
          })
        })
    }
  },
  computed: { // 计算：判断得到热门列表，搜索数据列表的是否显示结果
    hotPlace () {
      return !this.searchWord && this.isFocus
    },
    searchPlace () {
      return this.searchWord && this.isFocus
    }
  },
  created () { // 数据请求：热门和推荐
    api.searchHotWords().then(res => {
      this.hotSearchList = this.suggestList = res.data.data
    })
  }
}
</script>
<style lang="scss">
@import "@/assets/css/public/header/index.scss";
@import "@/assets/css/public/header/search.scss";
</style>
