import axios from 'axios'
axios.defaults.baseURL = 'http://open.duyiedu.com'// 在全局配置默认域名
axios.interceptors.request.use(function (config) {
  config.params = {
    ...config.params,
    appkey: 'WuTao_1583141476704'// 请求前设置参数
  }
  return config
}, function (error) {
  // 对请求错误做些什么
  return Promise.reject(error)
})
export default axios
