<template>
  <div class="page-login">
    <div class="login-header">
      <router-link to='/index' class="logo" href="http://www.meituan.com"></router-link>
    </div>
    <div class="login-panel">
      <div class="banner">
        <img
          src="https://s0.meituan.net/bs/file/?f=fe-sso-fs:build/page/static/banner/www.jpg"
          alt="美团网"
          width="480"
          height="370"
        />
      </div>
      <div class="form">
        <h4 class="tips" v-if="error">{{error}}</h4>
        <p>
          <span>账号登录</span>
        </p>
        <el-input :class="{error:error&&!userName}" v-model="userName" placeholder="账号" prefix-icon="profile" type='text'></el-input>
        <el-input :class="{error:error&&!password}" v-model="password" placeholder="密码" prefix-icon="password" type='password'></el-input>
        <div class="foot">
          <a href="#">忘记密码？</a>
        </div>
        <el-button type="primary" class="btn-login" @click='submitFn'>登录</el-button>
        <p class="reg">
          <span>还没有账号？</span>
          <router-link to="/register">免费注册</router-link>
        </p>
        <div class="oauth-wrapper">
          <h3 class="title-wrapper">
            <span class="title">用合作网站账号登录</span>
          </h3>
          <div class="oauth cf">
            <span
              class="oauth__link oauth__link--qq"
              data-href="/account/connect/tencent"
              target="_blank"
              id="J-third-tencent"
            ></span>
            <span
              class="oauth__link oauth__link--weibo third-login-btn"
              data-href="/account/connect/sina"
              target="_blank"
              id="J-third-sina"
            ></span>
          </div>
        </div>
      </div>
    </div>
    <footer>
      <ul>
        <li>
          <a href='#'>关于美团</a>
        </li>
        <li>
          <a href='#'>加入我们</a>
        </li>
        <li>
          <a href='#'>商家入驻</a>
        </li>
        <li>
          <a href='#'>帮助中心</a>
        </li>
        <li>
          <a href='#'>美团手机版</a>
        </li>
      </ul>
      <p>©2020 美团网团购 meituan.com 京ICP证070791号 京公网安备11010502025545号</p>
    </footer>
  </div>
</template>
<script>
import api from '@/api/index.js'
export default {
  data () {
    return {
      error: '',
      userName: '',
      password: ''
    }
  },
  methods: {
    submitFn () {
      if (!this.userName) {
        this.error = '请输入账号'
        return false
      }
      if (!this.password) {
        this.error = '请输入密码'
        return false
      }
      api.getLogin({
        params: {
          userName: this.userName,
          password: this.password
        }
      })
        .then(res => {
          if (res.data.status === 'success') {
            this.$router.push('/index')
            this.$store.commit('setUserName', this.userName)
          } else {
            this.error = res.data.msg
          }
        })
    }
  }
}
</script>
<style lang="scss">
@import "@/assets/css/login/index.scss";
</style>
