<template>
    <div class="m-hcity">
        <dl>
             <dt>{{theme}}</dt>
             <dd v-for="(item,index) in list" :key="index" @click='changeCity(item)'>{{item.name}}</dd>
        </dl>
    </div>
</template>
<script>
export default {
  props: ['theme', 'list'],
  methods: {
    changeCity (item) {
      this.$store.dispatch('setPosition', item)
      this.$router.push('/index')
    }
  }

}
</script>
<style lang="scss">
    @import "@/assets/css/changecity/hot.scss";
</style>
