<template>
  <div class="page-changeCity">
    <el-row>
      <province />
    </el-row>
    <el-row>
      <hot theme="热门城市:" :list="hotList" />
    </el-row>
    <el-row>
      <hot theme="最近访问:" :list="recentList" />
    </el-row>
    <el-row>
        <category/>
    </el-row>
  </div>
</template>
<script>
import province from '@/components/changeCity/province.vue'
import hot from '@/components/changeCity/hot.vue'
import category from '@/components/changeCity/category.vue'
import api from '@/api/index.js'
export default {
  data () {
    return {
      hotList: [],
      recentList: []
    }
  },
  components: {
    province,
    hot,
    category
  },
  created () {
    api.getHotCity()
      .then(res => {
        this.hotList = res.data.data
      })
    api.getRecentSearch()
      .then(res => {
        this.recentList = res.data.data
      })
  }
}
</script>
