<template>
  <div class="m-istyle">
    <dl @mouseover="mouseOverFn" :class="nav.class">
      <dt>{{nav.title}}</dt>
      <dd
        :class="{'active':kind==el.tab}"
        :data-type="el.tab"
        :key="el+'-'+i"
        v-for="(el,i) in nav.list"
      >{{el.text}}</dd>
    </dl>
    <ul class="ibody">
      <li :key="index" v-for="(item,index) in theData[kind]">
        <el-card :body-style="{ padding: '0px' }" shadow="never">
          <img :src="item.image" class="image" />
          <div class="cbody">
            <div class="title">{{item.title}}</div>
            <div class="sub-title">{{item.subTitle}}</div>
            <div class="price-info">
              <span class="current-price-wrapper">
                <span class="price-symbol numfont">￥</span>
                <span class="current-price numfont">{{item.price}}</span>
                <!-- <span class="old-price" v-if="item.oldPrice">门市价¥{{item.oldPrice}}</span> -->
              </span>
              <span class="sold bottom-right-info">{{item.address}}</span>
            </div>
          </div>
        </el-card>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  props: ['nav', 'theData'],
  data () {
    return {
      kind: 'all'
    }
  },
  methods: {
    mouseOverFn (e) {
      const dom = e.target
      const targetName = dom.tagName.toLowerCase()
      if (targetName !== 'dd') {
        return
      }
      const prop = e.target.getAttribute('data-type')
      this.kind = prop
      //    Ajax请求，动态获取数据
    }
  }
}
</script>
<style lang="scss">
@import "@/assets/css/index/artistic.scss";
</style>
