<template>
  <div class="category">
    <dl class="m-categroy">
      <dt>按拼音首字母选择：</dt>
      <dd v-for="item in letters" :key="item">
        <a :href="'#city-'+item">{{item}}</a>
      </dd>
    </dl>
    <dl class="m-categroy-section " v-for="(item,index) in cityGrop" :key="index" :id="'city-'+index">
      <dt>{{index}}</dt>
      <dd>
        <span @click='changeCity(el)' v-for='(el,index) in item' :key='index'>{{el.name}}</span>
      </dd>
    </dl>
  </div>
</template>
<script>
import api from '@/api/index.js'
export default {
  data () {
    return {
      letters: 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'.split(''),
      cityList: [],
      cityGrop: {}
    }
  },
  created () {
    api.getCityByOrder()
      .then(res => {
        // 将城市列表转换为按首字母排列的城市组
        this.cityList = res.data.data
        const obj = {}
        this.cityList.forEach((el, index) => {
          if (!obj[el.firstChar.toUpperCase()]) {
            obj[el.firstChar.toUpperCase()] = []
          }
          obj[el.firstChar.toUpperCase()].push(el)
        })

        const arr = []
        for (const prop in obj) {
          arr.push(prop)
        }
        arr.sort()// 将首字母数组排序
        const obj2 = {}
        arr.forEach((el, i) => {
          obj2[arr[i]] = obj[arr[i]]// 将上面的乱序城市组重新赋予有序
        })
        this.cityGrop = obj2
      })
  },
  methods: {
    changeCity (item) {
      this.$store.dispatch('setPosition', item)
      this.$router.push('/index')
    }
  }
}

</script>
<style lang="scss">
@import "@/assets/css/changecity/categroy.scss";
</style>
