<template>
    <div class="m-products-list">
        <ul>
            <li :class="{'s-nav-active':item.active}" v-for="item in nav" :key="item.key">{{item.name}}</li>
        </ul>
        <el-row>
            <item v-for="(el, index) in productList" :key="index" :meta= 'el'/>
        </el-row>
    </div>
</template>
<script>
import api from '@/api/index.js'
import item from '@/components/products/item.vue'
export default {
  data () {
    return {
      nav: [
        {
          key: 's-default',
          name: '智能排序',
          active: true
        },
        {
          key: 's-price',
          name: '价格最低',
          active: false
        },
        {
          key: 's-score',
          name: '人气最高',
          active: false
        },
        {
          key: 's-comment',
          name: '评价最高',
          active: false
        }
      ],
      productList: []
    }
  },
  components: {
    item
  },
  created () {
    api.getGoodList()
      .then(res => {
        this.productList = res.data.data
      })

    // 将数据中的评分由字符串类型转化为Number类型
    // this.productList = this.productList.map((el, index) => {
    //   return {...el, score: +el.score}
    // })
  }
}
</script>
