<template>
  <div :class="['choose-wrap','disabled'?disabled:'']" @click="showWrapper" v-document-click='documentClickFn'>
    <div  class="choose">
      <span >{{value}}</span>
      <i class="el-icon-caret-bottom"></i>
      <div :class="{'mt-content':true, 'active':showWrapperActive}">
        <h2>{{title}}</h2>
        <div :class="['wrapper',className]">
          <div class="col" v-for='(listData,index) in renderList' :key='listData+"~"+index'>
            <span  :class="{'mt-item':true,'active':item==value }" :key="index" v-for='(item,index) in listData' @click='changeValue($event,item)'>{{item.name}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ['value', 'title', 'list', 'showWrapperActive', 'className', 'disabled'],
  methods: {
    showWrapper (e) {
      e.stopPropagation()
      this.$emit('change-active', true)
    },
    documentClickFn () {
      this.$emit('change-active', false)
    },
    changeValue (event, val) {
      event.stopPropagation()
      this.$emit('change', val)
    }
  },
  computed: {
    renderList () {
      const col = Math.ceil(this.list.length / 12)
      const renderList = []
      for (let i = 0; i < col; i++) {
        const data = this.list.slice(i * 12, i * 12 + 12)
        renderList.push(data)
      }
      return renderList
    }
  }
}
</script>
<style lang="scss">
    @import "@/assets/css/changecity/select.scss";
</style>
