<template>
  <div class="m-menu">
    <dl class="nav">
      <dt>全部分类</dt>
      <dd :key="index" v-for="(item,index) in menuList" @mouseenter="mouseEnterFunc(item)" @mouseleave="mouseLeaveFunc">
        <i :class="item.type"></i>
        {{item.name}}
        <span class="arrow"></span>
      </dd>

      <div class="detail" v-if="datailData" @mouseenter="detailMouseEnter" @mouseleave="detailMouseLeave">
          <template  v-for="(item,index) in datailData">
              <h4 :key="item + '_' + index">{{item.title}}</h4>
              <span :key="item + '~' + j" v-for="(el,j) in item.items">{{el}}</span>
          </template>
      </div>
    </dl>
  </div>
</template>
<script>
import api from '@/api/index.js'
export default {
  data () {
    return {
      datailData: null,
      menuList: []
    }
  },
  methods: {
    mouseEnterFunc (item) {
      clearTimeout(this.timer)
      this.datailData = item.items
    },
    mouseLeaveFunc () {
      this.timer = setTimeout(() => {
        this.datailData = null
      }, 300)
    },
    detailMouseEnter () {
      clearTimeout(this.timer)
    },
    detailMouseLeave () {
      this.datailData = null
    }
  },
  created () { // 数据请求：导航列表
    api.nav()
      .then(res => {
        this.menuList = res.data.data
      })
  }
}
</script>
