<template>
  <div>
    <div>
      <span class="name">按省份选择：</span>
      <my-select
        class="province"
        :value="province"
        title="省份"
        :list="provinceList"
        :showWrapperActive="provinceActive"
        @change-active="changeProvinceActive"
        @change='changeProvince'
      />
      <my-select
        :disabled='disabled'
        :class="{province:true}"
        :value="city"
        title="城市"
        :list="cityList"
        :showWrapperActive="cityActive"
        @change-active="changeCityActive"
        @change='changeCity'
        className='city'
      />
      <span>直接搜索：</span>
      <el-select
        v-model="searchWord"
        filterable
        remote
        reserve-keyword
        placeholder="请输入关键词"
        :remote-method="remoteMethod"
        :loading="loading"
      >
        <el-option v-for="item in searchList" :key="item" :label="item" :value="item"></el-option>
      </el-select>
    </div>
  </div>
</template>
<script>
import mySelect from '@/components/changeCity/select.vue'
import api from '@/api/index.js'
export default {
  data () {
    return {
      province: '省份',
      provinceList: [],
      city: '城市',
      cityList: [],
      provinceActive: false,
      cityActive: false,
      searchList: ['哈尔滨', '佳木斯', '牡丹江', '鹤岗'],
      searchWord: '',
      loading: false,
      disabled: true
    }
  },
  components: {
    mySelect
  },
  methods: {
    // 由子组件触发
    changeProvinceActive (flag) {
      this.provinceActive = flag
      if (flag) {
        this.cityActive = false
        // 和cityActive达成互斥效果
      }
    },
    // 由子组件触发
    changeCityActive (flag) {
      if (!this.disabled) {
        this.cityActive = flag
        if (flag) {
          this.provinceActive = false
        // 和provinceActive达成互斥效果
        }
      }
    },
    remoteMethod (val) {
      // 请求后端接口
      console.log(document.cookie, localStorage)
    },
    changeCity (item) {
      this.city = item.name
      this.cityActive = false
      this.$store.dispatch('setPosition', item)
      this.$router.push('/index')
      console.log(item)
    },
    changeProvince (item) {
      this.province = item.name
      this.provinceActive = false
      this.disabled = false
      this.cityList = item.cityInfoList
    }

  },
  created () {
    api.getProvinceData()
      .then(res => {
        this.provinceList = res.data.data.map(el => {
          el.name = el.provinceName
          return el
        })
      })
  }
}
</script>
<style lang="scss">
@import "@/assets/css/changecity/iselect.scss";
</style>
